/* Vertical Rhythm Reset: Base */
*,
*:before,
*:after {
  box-sizing: inherit;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
  font-family: inherit;
  font-size: inherit;
  vertical-align: bottom; }

html {
  box-sizing: border-box;
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

body {
  margin: 0;
  background: #fff;
  color: #000; }

button,
fieldset,
input:not([type="radio"]):not([type="checkbox"]),
select,
textarea {
  border-width: 1px;
  border-style: solid;
  border-color: #808080; }

em,
i {
  font-style: italic; }

fieldset {
  padding-top: 0.01em;
  padding-bottom: 0;
  min-width: 0; }
  body:not(:-moz-handler-blocked) fieldset {
    display: table-cell; }

iframe {
  border: 0; }

small,
sub,
sup {
  font-size: 0.875em; }

input[type="radio"],
input[type="checkbox"] {
  margin-bottom: 0.325em; }

hr {
  position: relative;
  border: 0; }
  hr:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    height: 1px;
    background: #808080; }

sub,
sup {
  position: relative;
  line-height: 0; }

sub {
  vertical-align: sub; }

sup {
  vertical-align: super; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

article,
